import { Component, Vue } from 'vue-property-decorator';
import { KafkaMessageProvider } from '@/providers/KafkaMessageProvider';

@Component
export default class KafkaDLQTable extends Vue {
  public columns = [
    { label: 'Offset', field: 'name', filterOptions: { enabled: true } },
    { label: 'Key', field: 'age', type: 'number', filterOptions: { enabled: true }, width: '150px' },
    { label: 'Header', field: 'createdAt', filterOptions: { enabled: true } },
    { label: 'Payload', field: 'createdAt', filterOptions: { enabled: true } },
    { label: 'Timestamp', field: 'score', type: 'number', filterOptions: { enabled: true } },
    { label: 'Actions', field: 'actions', tdClass: 'text-center', width: '50px' },
  ];
  public rows: any[] = [];
  private kafkaMessageProvider: KafkaMessageProvider;

  constructor() {
    super();
    this.kafkaMessageProvider = new KafkaMessageProvider();
  }

  async mounted() {
    this.rows = await this.kafkaMessageProvider.fetch();
  }
}
