import { RESTProvider } from '@/providers/RESTProvider';
import { KafkaMessage } from '@/model/KafkaMessage';
import { deserialize } from '@/utils/object-mapper';

export class KafkaMessageProvider extends RESTProvider {
  protected getResource(): string {
    return '/kafka-messages';
  }

  public async fetch(): Promise<Array<KafkaMessage>> {
    const result = await this._fetch();

    return deserialize(result.data, KafkaMessage);
  }
}